<script>
import { products, accesories, materials } from '@/constants/products'
export default {
    name: 'product-view',
    data() {
        return {
            selectedProduct: {
                name: '',
                desc1: '',
                desc2: '',
                desc3: '',
                desc4: '',
                desc5: '',
                desc6: '',
                desc7: '',
                desc8: '',
                desc9: '',
                desc10: '',
                desc11: '',
                desc12: '',
                desc13: '',
                imageUrl: '',
                PDFurl: '',
            },
            isImageModalActive: false,
            typeOfProduct: 0,
            dinamicImage: '',
            product_list: products,
            accesories_list: accesories,
            materials_list: materials,
        }
    },
    methods: {
        showProduct(product,typeOfProduct) {
            if(typeOfProduct == 1) {
                this.dinamicImage = require(`@/assets/products/accesorios/1.jpg`)
            }
            if(typeOfProduct == 2){
                this.dinamicImage = require(`@/assets/products/materiales/1.jpg`)
            }
            this.typeOfProduct = typeOfProduct
            this.selectedProduct = product
            this.isImageModalActive = true
            
        },

        changeImage(number){
            if(this.typeOfProduct == 1) {
                this.dinamicImage = require(`@/assets/products/accesorios/${number}.jpg`)
            } else {
                this.dinamicImage = require(`@/assets/products/materiales/${number}.jpg`)
            }
        },
    },
}
</script>

